<template>

  <transition name="fade">
      <Modal @closeModal="모달창열렸니 = false;" :원룸들="원룸들" :선택="선택" :모달창열렸니="모달창열렸니"/>
  </transition>


  <div class="menu">
    <a v-for="(a, i) in memu" :key="i"> {{ a }} </a>
  </div>

    <!-- <TheDiscount v-if="showDiscount == true" /> -->
    <div class="discount">
      <!-- <h4>지금 결제하면 {{amount}}% 할인</h4> -->
    </div>

    <button @click="priceSort">가격순정렬</button>
    <button @click="sortBack">되돌리기</button>
   
    <Card @openModal="모달창열렸니 = true; 선택 = $event" :원룸="원룸들[i]" v-for="(a, i) in 원룸들" :key="i"/>
    
  


</template>

<script>

import data from './oneroom.js';
import TheDiscount from './components/TheDiscount.vue';
import Modal from './components/Modal.vue'
import Card from './components/Card.vue'

export default {
  name: 'App',
  data(){
    return {
      amount : 30,
      showDiscount : true,
      원룸들오리지널 : [...data],
      선택 : 0,
      원룸들 : data,
      모달창열렸니 : false,
      신고수 : [9, 0, 7],
      memu : ['Home', 'Shop', 'About',],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸']
    }
  },
  methods : {
    increase(){
      this.신고수 += 1;
    },
    sortBack(){
      this.원룸들 = [...this.원룸들오리지널];
    },
    priceSort(){
      this.원룸들.sort(function(a,b){
        return a.price - b.price
      });
    }
  },

  mounted(){
    setInterval(() => {
      this.amount--;
    }, 1000);
  },

  components: {
    TheDiscount : TheDiscount,
    Modal : Modal,
    Card : Card,
  }
}
</script>

