<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <h4>{{원룸들[선택].title}}</h4>
      <img style="width: 350px" :src="원룸들[선택].image">
      <p>{{원룸들[선택].content}}</p>
      <input v-model="month">
      <p style="fontWeight:bold;" >{{month}}개월 할부 : {{원룸들[선택].price * month}}원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div> 
</template>

<script>
export default {
    name : 'Modal',
    data(){
      return {
        month : 1,
      }
    },
watch: {
  
 
month(a) {
  if (isNaN(a)) {
      alert('숫자만 입력해 주세요');
      this.month = 1;
  } else if (a >= 13) {
      alert('12개월 이하로 입력해주세요');
      this.month = 1;
    }
  },
},

    props : {
      원룸들 : Array,
      선택 : Number,
      모달창열렸니 : Boolean,
    }
}
</script>

